import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';
import { LoadingController } from 'ionic-angular';
import { ModalController } from 'ionic-angular';

/**
 * Generated class for the ReservarPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-reservar',
  templateUrl: 'reservar.html',
})
export class ReservarPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public apiProvider: ApiProvider, public loadingController:LoadingController, public modalCtrl : ModalController) {
  }


  ionViewDidLoad() {

    let loading = this.loadingController.create({content : "cargando..."});
    loading.present();

    return this.apiProvider.getClasesReserva()
    .then(data => {
       console.log(data);
  	  loading.dismissAll();
    });

    }

}



